html {
  font-size: 14px;
  height: 100%;
  background: #3D4149;
}
body {
	width: 100%;
  background-color: #3d4149;
}
.page {
	position: relative;
	background: #3D4149;
}
.new-task{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: baseline;
	align-items: center;
	-ms-flex-pack: justify;
	justify-content: flex-start;
	text-align: right;
}
.new-task span{
	width: 25%;
	text-align: left;
	overflow: hidden;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
}
.new-task span em{
	overflow: hidden;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
}
.my-task-block button{
	color: #fff;
    background-color: #0275d8;
    border-color: #0275d8;
}
.completed-task span{
	display: inline-block;
	width: 25%;
	text-align: left;
	overflow: hidden;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
}
.new-task .new-task-name {
	width: auto;
	min-width: 55px;
	color: #FFFFFF;
}
.page-taskcomplite button {
	margin-left: 50px;
}



