a:hover {
	text-decoration: none;
}
.form-addgoal {
	margin-top: 60px;
}
.form-inline .form-group {
	display: block;
}
.form-group-addgoal {
	display: inline-block;
	width: auto!important;
	text-align: center;
	color: #FFFFFF;
	font-size: 20px;
	font-weight: bold; border-radius: 10px;
	padding: 5px 10px!important;
	-webkit-box-shadow: 5px 5px 13px 0px rgba(0, 0, 0, 0.14), inset 0px 0px 40px 0px rgba(255, 255, 255, 0.05);
	box-shadow:         5px 5px 13px 0px rgba(0, 0, 0, 0.14), inset 0px 0px 40px 0px rgba(255, 255, 255, 0.05);
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: baseline;
	align-items: center;
	-ms-flex-pack: justify;
	justify-content: space-between;
}
.input-addgoal {
	display: inline-block;
}
.form-inline .form-control {
	width: 250px;
}
.form-group-title{
	width: 100%;
	line-height: 1;
	padding-bottom: 3px;
}
.form-group input{
	background: #F3FBE3;
	-webkit-box-shadow: 4px 7px 6px 0px rgba(0, 0, 0, 0.14);
	box-shadow:         4px 7px 6px 0px rgba(0, 0, 0, 0.14);
}
.form-group button{
	color: #3D4149;
	font-weight: bold;
	border: none;
	cursor: pointer;
	background: rgba(69,225,189,1);
	background: -webkit-linear-gradient(0deg, rgba(69,225,189,1) 0%,rgba(119,233,207,1) 100%);
	background: -webkit-gradient(linear, left bottom, left top, from(rgba(69,225,189,1)),to(rgba(119,233,207,1)));
	background: -webkit-linear-gradient(bottom, rgba(69,225,189,1) 0%,rgba(119,233,207,1) 100%);
	background: -o-linear-gradient(bottom, rgba(69,225,189,1) 0%,rgba(119,233,207,1) 100%);
	background: linear-gradient(0deg, rgba(69,225,189,1) 0%,rgba(119,233,207,1) 100%);
	-webkit-box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.43);
	box-shadow:         0px 1px 2px 0px rgba(0, 0, 0, 0.43);
}
.form-group button:hover{
	color: #3D4149;
}


